<template>
    <div>
        <el-progress :percentage="(packets.length / packetsCount) * 100" type="circle" :format="formatPercent"></el-progress>
        <div>{{ packets.length }} / {{ packetsCount }} packets</div>
        <br>
        <div>
            <el-button @click="filters.done = ! filters.done" :type="filters.done ? 'success' : 'info'">Done</el-button>
            <el-button @click="filters.todo = ! filters.todo" :type="filters.todo ? 'primary' : 'info'">Todo</el-button>
            <el-button @click="filters.impossible = ! filters.impossible" :type="filters.impossible ? 'danger' : 'info'">Unsniffable</el-button>
<!--            <el-button @click="filters.unused = ! filters.unused" :type="filters.unused ? 'warning' : 'info'">Unused</el-button>-->
        </div>
        <br>
        <span v-if="filters.done">
            <el-tag v-for="(packet, index) in packets" :key="index + 10000" size="medium" :type="packet.type">{{packet.name}}</el-tag>
        </span>
        <span v-if="filters.todo">
            <el-tag v-for="(name, index) in packetsName" :key="index" size="medium">{{name}}</el-tag>
        </span>
        <span v-if="filters.impossible">
            <el-tag v-for="(name, index) in notSniffable" :key="-index - 10" size="medium" type="danger">{{name}}</el-tag>
        </span>
    </div>
</template>

<script>
    import {namesE} from "@/data/packetName158";
    import {rawMapping} from "@/data/1_68_0.wfkmap";
    import {structurePackets} from "@/data/structurePackets";
    import {notSniffablePackets} from "@/data/noSniffable";
    import {unusedPackets} from "@/data/unused";

    export default {
        name: "PacketsPage",
        mounted() {
            let names = namesE.slice();
            this.packetsCount = names.length;
            let packets = rawMapping.split("\n");
            for (let p of packets) {
                let def = p.split("=");
                if (def.length !== 2) continue;
                let name = def[1].trim();
                let type;
                let index = names.indexOf(name);
                type = 'success'; // tous les packets sont bon s'ils viennent de la map ¯\_(ツ)_/¯
                if (index !== -1) {
                    // type = 'success';
                    names.splice(index, 1)
                } else {
                    // type = '';
                }
                this.packets.push({
                    op: def[0],
                    name: name,
                    type: type
                });
            }
            structurePackets.forEach(p => {
                this.packets.push({type: 'success', name: p});
                let index = names.indexOf(p);
                if (index !== -1) {
                    names.splice(index, 1);
                }
            });
            this.packets.sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0))
            let toDelete = [];
            names.forEach((v, i) => {
                if (notSniffablePackets.indexOf(v) !== -1 || unusedPackets.indexOf(v) !== -1) {
                    this.notSniffable.push(v);
                    toDelete.push(i);
                }
            });
            for (let i = toDelete.length - 1; i >= 0; i--) {
                names.splice(toDelete[i], 1)
            }
            names.sort()
            this.notSniffable.sort();
            this.packetsName = names;
            this.packetsCount = this.packetsCount - this.notSniffable.length;
        },
        data() {
            return {
                filters: {
                    done: false,
                    todo: true,
                    impossible: false,
                    unused: false
                },
                packetsCount : 1,
                packets: [],
                packetsName: [],
                notSniffable : []
            }
        },
        methods: {
            formatPercent(percent) {
                return percent.toFixed(2) + '%';
            }
        }
    }
</script>